"use client";

import React from "react";
import Image from "next/image";
import { useRouter } from "next/navigation";

interface DrdHeaderProps {
  step: 1 | 2 | 3;
}

/**
 * DRD 전용 헤더 컴포넌트
 * Figma 디자인을 바탕으로 1(Default Settings), 2(Simulation Settings), 3(Dash Board) 단계를 표시합니다.
 */
export const DrdHeader = ({ step }: DrdHeaderProps) => {
  const router = useRouter();

  const handleGoBack = () => {
    if (step === 1) {
      router.push("/");
    } else if (step === 2) {
      router.push("/drd/default-setting");
    }
  };

  return (
    <header className="flex items-center justify-between px-[28px] py-[24px] h-[90px] w-full bg-[#E7E5E7] sticky top-0 z-[90] shrink-0">
      {/* ── 왼쪽: 단계별 상태 ────────────────────────────────────────────────── */}
      <div className="flex items-center gap-[36px]">
        {/* Step 1: Default Settings */}
        <button
          onClick={() => router.push("/drd/default-setting")}
          className="flex items-center gap-[10px] bg-transparent border-none p-0 cursor-pointer"
        >
          <div className={`flex items-center justify-center w-[22px] h-[22px] rounded-full ${step === 1 ? 'bg-[#262255]' : 'bg-[#929090]'}`}>
            <span className="text-white text-[14px] font-bold leading-none">1</span>
          </div>
          <span className={`text-[22px] font-semibold tracking-[-0.88px] ${step === 1 ? 'text-[#262255]' : 'text-[#787776]'}`}>
            Default Settings
          </span>
        </button>

        {/* Arrow / Indicator */}
        <div className="flex items-center justify-center w-[24px] h-[24px] rounded-full bg-[#B2B1B1] shadow-[0px_1px_2px_rgba(0,0,0,0.1)]">
          <svg width="8" height="12" viewBox="0 0 8 12" fill="none">
            <path d="M2 2L6 6L2 10" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </div>

        {/* Step 2: Simulation Settings */}
        <button
          onClick={() => router.push("/drd/simulation-setting")}
          className="flex items-center gap-[10px] bg-transparent border-none p-0 cursor-pointer"
        >
          <div className={`flex items-center justify-center w-[22px] h-[22px] rounded-full ${step === 2 ? 'bg-[#262255]' : 'bg-[#929090]'}`}>
            <span className="text-white text-[14px] font-bold leading-none">2</span>
          </div>
          <span className={`text-[22px] font-semibold tracking-[-0.88px] ${step === 2 ? 'text-[#262255]' : 'text-[#787776]'}`}>
            Simulation Settings
          </span>
        </button>

        {/* Arrow / Indicator */}
        <div className="flex items-center justify-center w-[24px] h-[24px] rounded-full bg-[#B2B1B1] shadow-[0px_1px_2px_rgba(0,0,0,0.1)]">
          <svg width="8" height="12" viewBox="0 0 8 12" fill="none">
            <path d="M2 2L6 6L2 10" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </div>

        {/* Step 3: Dash Board */}
        <button
          onClick={() => router.push("/drd/simulation-result")}
          className="flex items-center gap-[10px] bg-transparent border-none p-0 cursor-pointer"
        >
          <div className={`flex items-center justify-center w-[22px] h-[22px] rounded-full ${step === 3 ? 'bg-[#262255]' : 'bg-[#929090]'}`}>
            <span className="text-white text-[14px] font-bold leading-none">3</span>
          </div>
          <span className={`text-[22px] font-semibold tracking-[-0.88px] ${step === 3 ? 'text-[#262255]' : 'text-[#787776]'}`}>
            Dash Board
          </span>
        </button>
      </div>

      {/* ── 오른쪽: 버튼 그룹 (Variant 3: Go Back + Help) — step 3에서 숨김 ── */}
      {step !== 3 && (
        <div className="flex items-center gap-[12px]">
          {/* Go Back 버튼 — Glass Button / Light-Secondary / Icon Only */}
          <button
            onClick={handleGoBack}
            title="Go Back"
            className="relative w-[44px] h-[44px] rounded-[32px] shadow-[0px_0px_2px_0px_rgba(0,0,0,0.05)] flex items-center justify-center overflow-hidden"
          >
            <div className="absolute inset-0 rounded-[32px] bg-white/10 mix-blend-screen" />
            <div className="absolute inset-0 rounded-[32px] border-2 border-[rgba(255,255,255,0.3)]" />
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" className="relative z-10">
              <path d="M19 12H5M5 12L12 19M5 12L12 5" stroke="#484646" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
            </svg>
          </button>

          {/* Help 버튼 — Glass Button / Light-Secondary / Icon Only */}
          <button
            title="Help"
            className="relative w-[44px] h-[44px] rounded-[36px] shadow-[0px_0px_2px_0px_rgba(0,0,0,0.05)] flex items-center justify-center overflow-hidden"
          >
            <div className="absolute inset-0 rounded-[36px] bg-white/10 mix-blend-screen" />
            <div className="absolute inset-0 rounded-[36px] border-2 border-[rgba(255,255,255,0.3)]" />
            <span className="relative z-10 text-[#484646] font-bold text-[18px] leading-none">?</span>
          </button>
        </div>
      )}
    </header>
  );
};
