"use client";

interface CustomCheckboxProps {
  checked: boolean;
  onChange: () => void;
  size?: number;
  className?: string;
}

export default function CustomCheckbox({
  checked,
  onChange,
  size = 20,
  className = "",
}: CustomCheckboxProps) {
  return (
    <button
      type="button"
      onClick={onChange}
      className={`cursor-pointer bg-transparent border-none p-0 flex items-center justify-center shrink-0 ${className}`}
      aria-checked={checked}
      role="checkbox"
    >
      <svg width={size} height={size} viewBox="0 0 20 20" fill="none">
        {checked ? (
          <>
            <rect width="20" height="20" rx="5" fill="#5B4EFF" />
            <path
              d="M5 10.5L8.5 14L15 7"
              stroke="white"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
            />
          </>
        ) : (
          <rect
            x="1"
            y="1"
            width="18"
            height="18"
            rx="4"
            stroke="#C7C5C9"
            strokeWidth="1.5"
            fill="white"
          />
        )}
      </svg>
    </button>
  );
}
