/* Figma Disclosure 아이콘 — public SVG 사용 (size=20) */
function DisclosureIcon() {
  return (
    // eslint-disable-next-line @next/next/no-img-element
    <img
      src="/icons/disclosure/Property 1=Close, Size=18.svg"
      alt=""
      width={20}
      height={20}
      style={{ flexShrink: 0, display: "block" }}
    />
  );
}

/* Figma DropdownItem — height=36, bg=#efeff4, rounded-[8px] */
function DropdownCell({ value, width, placeholder }: { value: string; width?: number | string; placeholder?: boolean }) {
  return (
    <div
      style={{
        width: width,
        height: 36,
        background: "#efeff4",
        borderRadius: 8,
        display: "flex",
        alignItems: "center",
        paddingLeft: 8,
        paddingRight: 6,
        gap: 4,
        cursor: "pointer",
        flexShrink: 0,
      }}
    >
      <span
        style={{
          flex: 1,
          fontFamily: "Inter",
          fontWeight: 500,
          fontSize: 15,
          color: placeholder ? "#c6c5c9" : "#484646",
          letterSpacing: "-0.45px",
          lineHeight: 1.1,
          whiteSpace: "nowrap",
          overflow: "hidden",
          textOverflow: "ellipsis",
        }}
      >
        {value}
      </span>
      <DisclosureIcon />
    </div>
  );
}

