// ─── 글래스 Test 버튼 ───────────────────────────────────────────────────────

function GlassTestButton() {
  return (
    <div
      style={{
        position: "relative",
        height: 44,
        paddingLeft: 20,
        paddingRight: 20,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        cursor: "pointer",
        flexShrink: 0,
      }}
    >
      <div
        style={{
          position: "absolute",
          inset: 0,
          borderRadius: 36,
          background: "#242424",
          mixBlendMode: "screen",
          opacity: 0.3,
        }}
      />
      <div
        style={{
          position: "absolute",
          inset: 0,
          borderRadius: 36,
          border: "2px solid rgba(255,255,255,0.3)",
          boxShadow: "0px 0px 2px 0px rgba(0,0,0,0.05)",
        }}
      />
      <span
        style={{
          position: "relative",
          zIndex: 1,
          fontFamily: "Inter, sans-serif",
          fontWeight: 600,
          fontSize: 15,
          color: "rgb(72,70,70)",
          letterSpacing: "-0.45px",
          whiteSpace: "nowrap",
        }}
      >
        Test
      </span>
    </div>
  );
}

// ──────────────────────────────────────────────────────────────────────────

export default function FilterPage() {
  const router = useRouter();
  const setCompleted = useDefaultSettingStore((s) => s.setCompleted);
  const [activeTab, setActiveTab] = useState<"Inclusion" | "Exclusion">("Inclusion");
  const [checkedRows, setCheckedRows] = useState<Record<string, boolean>>({});

  return (
    <AppLayout headerType="drd" drdStep={1}>
    <div style={{ display: "flex", flexDirection: "column", width: "100%", height: "100%", gap: 24, overflow: "hidden" }}>
        {/* ── TOP: Title ───────────────────────────── */}
        <div style={{ display: "flex", flexDirection: "row", alignItems: "flex-start", justifyContent: "space-between", flexShrink: 0 }}>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <h1 style={{ fontFamily: "Poppins, Inter, sans-serif", fontSize: 42, fontWeight: 600, color: "rgb(17,17,17)", letterSpacing: "-1.5px", lineHeight: 1.1, margin: 0 }}>
              Default Settings
            </h1>
            <span style={{ fontFamily: "Inter", fontSize: 16, fontWeight: 600, color: "rgb(120,119,118)", letterSpacing: "-0.48px" }}>
              Setup Required
            </span>
          </div>
        </div>
        
        {/* ── ② 컨텐츠 행 ──────────────────────────────────────────────── */}
        <div className="flex  gap-[12px] min-h-0 items-stretch">
          
          {/* ── 왼쪽 패널 (Navy Glass) ────────────────── */}
          <div
            className="drd-left-panel flex-[22] min-w-[280px] min-h-0"
            style={{
              borderImage: 'url("/assets/figma/home/frame-panel-left.png") 72 fill / 36px / 0 stretch',
              borderStyle: "solid",
              borderWidth: "30px",
              borderColor: "transparent",
            }}
          >
            
            {/* Filtered Patients 카드 */}
            <div className="shrink-0 h-[250px] relative rounded-[24px] overflow-hidden flex flex-col p-[16px] shadow-[0px_0px_2px_0px_rgba(0,0,0,0.1)]">
              <div 
                className="absolute inset-0 z-0 pointer-events-none" 
                style={{ backgroundImage: "linear-gradient(90deg, #262255 0%, #262255 100%)" }} 
              />
              <div className="absolute inset-0 bg-[rgba(38,38,38,0.25)] mix-blend-color-dodge z-[1]" />

              <div className="relative z-10 flex flex-col h-full gap-[8px]">
                <div className="flex justify-between items-start">
                  <div className="flex flex-col gap-[4px]">
                    <span className="font-['Inter'] font-semibold text-[12px] leading-[1.18] text-white tracking-[-0.36px]">
                      filtered patients
                    </span>
                    <span className="font-['Inter'] font-semibold text-[36px] leading-none text-white tracking-[-1.08px]">
                      100%
                    </span>
                  </div>
                  <button className="flex items-center gap-[4px] h-[30px] px-[14px] py-[8px] rounded-[36px] border-none cursor-pointer relative bg-transparent overflow-hidden">
                    <div className="absolute inset-0 bg-[#f06600] mix-blend-plus-lighter" />
                    <span className="relative z-10 font-['Inter'] font-semibold text-[15px] leading-[1.15] text-white tracking-[-0.75px] mix-blend-screen">
                      Add data
                    </span>
                    <span className="relative z-10 text-[16px] text-white font-bold mix-blend-screen">+</span>
                  </button>
                </div>

                <div className="flex flex-col mt-auto gap-[8px]">
                  <div className="relative h-[18px] w-full rounded-[12px] bg-[rgba(0,0,0,0.2)] overflow-hidden">
                    <div className="absolute left-0 top-0 h-full w-[324px] bg-[#f06600] rounded-[12px]" />
                    <div className="absolute inset-0 flex items-center justify-end pr-[11px]">
                      <span className="font-['Inter'] font-semibold text-[12px] leading-[1.18] text-white tracking-[-0.36px] mix-blend-screen">
                        360/360 patients
                      </span>
                    </div>
                  </div>
                  <div className="flex flex-col gap-[2px]">
                    <div className="font-['Inter'] font-semibold text-[15px] leading-[1.15] text-white tracking-[-0.75px]">
                      OPMD
                    </div>
                    <div className="flex gap-[11px]">
                      <div className="flex gap-[3px] items-center">
                        <span className="font-['Inter'] font-semibold text-[10px] leading-[1.1] text-white tracking-[-0.4px]">Initial Cohort</span>
                        <span className="font-['Inter'] font-semibold text-[15px] leading-[1.15] text-white tracking-[-0.75px] w-[86px]">4,800</span>
                      </div>
                      <div className="flex gap-[3px] items-center">
                        <span className="font-['Inter'] font-semibold text-[10px] leading-[1.1] text-white tracking-[-0.4px]">Final Cohort</span>
                        <span className="font-['Inter'] font-semibold text-[15px] leading-[1.15] text-white tracking-[-0.75px] w-[86px]">4,800</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* Setup Steps */}
            <div className="flex-1 rounded-[24px] bg-[rgba(255,255,255,0.6)] flex flex-col p-[16px] gap-[30px] overflow-hidden">
              {setupSteps.map((step) => (
                <button
                  key={step.id}
                  onClick={() => router.push(stepRoutes[step.id])}
                  className={[
                    "flex flex-col p-[16px] rounded-[24px] pt-[12px] pb-[16px] shrink-0 border-none cursor-pointer text-left w-full transition-colors duration-150",
                    step.isActive
                      ? ""
                      : "hover:bg-[#f9f8fc] active:bg-[#efeff4]",
                  ].join(" ")}
                  style={{ backgroundColor: step.bgColor || undefined }}
                >
                  <div className="flex items-center gap-[18px]">
                    <div className="shrink-0 flex items-center justify-center">
                      <step.IconComponent size={24} />
                    </div>
                    <span
                      className="font-['Inter'] font-semibold text-[19.5px] leading-[1.12] tracking-[-0.78px]"
                      style={{ color: step.titleColor }}
                    >
                      {step.title}
                    </span>
                  </div>
                  <div className="pl-[42px] mt-0">
                    <p
                      className="font-['Inter'] font-semibold text-[10px] leading-[1.1] tracking-[-0.4px] m-0"
                      style={{ color: step.descriptionColor }}
                    >
                      {step.description}
                    </p>
                  </div>
                </button>
              ))}
            </div>
          </div>

          {/* ── 오른쪽 패널 (Light Glass) ────────────────── */}
          {/* 오른쪽 상위 배경 카드: selection-bg.png → 안에 흰색 테이블 카드 */}
           <div className="drd-right-panel flex-[78] min-w-0 min-h-0"> 
            <div className="shrink-0 px-[8px] pt-[12px] flex items-center justify-between">
              <h2 className="font-['Inter'] font-semibold text-[24px] leading-[1.2] text-[#262255] tracking-[-0.72px] m-0">
                Filter
              </h2>
              <GlassTestButton />
            </div>

            <div className="flex flex-1 min-h-0 gap-[12px]">
              
              {/* Feature List (왼쪽 컬럼) */}
              <div className="w-[272px] shrink-0 flex flex-col gap-[12px]">
                <div className="px-[4px]">
                  <span className="font-['Inter'] font-medium text-[19.5px] leading-[1.2] text-[#484646] tracking-[-0.585px]">Feature List</span>
                </div>
                
                {/* 검색 필드 */}
                <div className="h-[44px] bg-white rounded-[36px] flex items-center px-[18px] gap-[8px]">
                  <IconSearch size={20} />
                  <span className="font-['Inter'] font-medium text-[15px] text-[#c6c5c9] tracking-[-0.45px]">Search features</span>
                </div>

                {/* 리스트 아코디언 */}
                <div className="flex-1 bg-white rounded-[24px] flex flex-col overflow-hidden font-['Inter']">
                  {featureCategories.map((cat, idx) => (
                    <div key={cat.name} className="flex flex-col border-b border-[#c7c5c9] last:border-none">
                      <div className={`flex items-center h-[48px] px-[18px] gap-[10px] ${cat.active ? "bg-[#262255]" : ""}`}>
                        <div className="shrink-0 flex items-center justify-center -rotate-90">
                          {cat.open ? <IconChevronDown size={14} color={cat.active ? "#ffffff" : "#484646"} /> : <IconChevronRight size={14} color={cat.active ? "#ffffff" : "#484646"} />}
                        </div>
                        <span className={`font-semibold text-[17px] tracking-[-0.51px] ${cat.active ? "text-white" : "text-[#484646]"}`}>
                          {cat.name}
                        </span>
                      </div>
                      {cat.active && (
                        <div className="flex flex-col overflow-y-auto scrollbar-hide py-[8px]">
                          {infoFeatures.map(f => (
                            <div key={f} className="h-[44px] flex items-center pl-[44px] text-[17px] text-[#919092] font-medium tracking-[-0.68px] border-t border-[#c7c5c9] first:border-none">
                              {f}
                            </div>
                          ))}
                        </div>
                      )}
                    </div>
                  ))}
                </div>
              </div>

              {/* 메인 설정 영역 (오른쪽 컬럼) */}
              <div className="flex-1 flex flex-col gap-[12px] rounded-[24px] bg-[rgba(255,255,255,0.6)] p-[16px] overflow-hidden">
                
                {/* 상단 탭 + 액션 버튼 */}
                <div className="flex justify-between items-center shrink-0">
                  {/* Inclusion / Exclusion 탭 */}
                  <div className="bg-transparent p-[4px] rounded-[36px] flex">
                    <button 
                      onClick={() => setActiveTab("Inclusion")}
                      className={`h-[36px] px-[18px] rounded-[36px] border-none font-semibold text-[15px] cursor-pointer transition-all ${activeTab === "Inclusion" ? "bg-[#262255] text-white" : "bg-transparent text-[#484646]"}`}
                    >
                      Inclusion
                    </button>
                    <button 
                      onClick={() => setActiveTab("Exclusion")}
                      className={`h-[36px] px-[18px] rounded-[36px] border-none font-semibold text-[15px] cursor-pointer transition-all ${activeTab === "Exclusion" ? "bg-[#262255] text-white" : "bg-transparent text-[#484646]"}`}
                    >
                      Exclusion
                    </button>
                  </div>

                  {/* 유리 스타일 버튼들 */}
                  <div className="flex items-center gap-[12px]">
                    <div className="flex gap-[4px]">
                      <button className="size-[48px] rounded-[36px] border-2 border-solid border-[rgba(255,255,255,0.3)] bg-[rgba(36,36,36,0.3)] flex items-center justify-center cursor-pointer shadow-[0px_0px_2px_rgba(0,0,0,0.05)]">
                        <IconFileDownload size={24} />
                      </button>
                      <button className="size-[48px] rounded-[36px] border-2 border-solid border-[rgba(255,255,255,0.3)] bg-[rgba(36,36,36,0.3)] flex items-center justify-center cursor-pointer shadow-[0px_0px_2px_rgba(0,0,0,0.05)]">
                        <IconFolderPlus size={24} />
                      </button>
                      <button className="size-[48px] rounded-[36px] border-2 border-solid border-[rgba(255,255,255,0.3)] bg-[rgba(36,36,36,0.3)] flex items-center justify-center cursor-pointer shadow-[0px_0px_2px_rgba(0,0,0,0.05)]">
                        <IconTrash size={24} />
                      </button>
                    </div>
                    {/* Add Section 버튼 */}
                    <button className="h-[48px] px-[20px] rounded-[100px] border-2 border-solid border-[rgba(255,255,255,0.3)] bg-[rgba(36,36,36,0.3)] shadow-[0px_0px_2px_rgba(0,0,0,0.05)] flex items-center gap-[4px] cursor-pointer">
                      <span className="font-['Inter'] font-semibold text-[15px] text-[#c6c5c9] tracking-[-0.75px]">Add Section</span>
                      <IconPlus size={20} />
                    </button>
                  </div>
                </div>

                {/* 섹션 리스트 */}
                <div className="flex-1 overflow-y-auto scrollbar-hide flex flex-col gap-[14px]">
                  {sections.map(section => (
                    <div key={section.id} className="bg-white rounded-[12px] flex flex-col border border-solid border-transparent">
                      {/* 섹션 헤더 */}
                      <div className="h-[46px] flex items-center px-[21px] gap-[10px] border-b border-[#c7c5c9]">
                        <div className="shrink-0 -rotate-90">
                          <IconChevronDown size={14} color="#313030" />
                        </div>
                        <span className="font-['Inter'] font-semibold text-[17px] text-[#313030] tracking-[-0.51px]">
                          {section.name}
                        </span>
                      </div>
                      
                      {/* 섹션 본문 - 첫 번째 줄 */}
                      <div className="flex items-center h-[50px] px-[24px] gap-[14px]">
                        <CustomCheckbox
                          checked={!!checkedRows[`${section.id}-main`]}
                          onChange={() => setCheckedRows(prev => ({ ...prev, [`${section.id}-main`]: !prev[`${section.id}-main`] }))}
                        />
                        <div className="w-[150px]" /> {/* 빈 공간 / Or 자리 */}

                        <DropdownCell value={section.feature} width={240} />
                        <DropdownCell value={section.op} width={160} />
                        <DropdownCell value={section.value} width={336} />

                        <div className="w-[16px] flex items-center justify-center opacity-0 hover:opacity-100"><IconPlus size={16} color="#999" /></div>
                      </div>

                      {/* 섹션 본문 - 추가 줄 (서브 로직) */}
                      {section.subRows?.map((row, rIdx) => (
                        <div key={rIdx} className="flex items-center h-[50px] px-[24px] gap-[14px] border-t border-[#c7c5c9]">
                          <CustomCheckbox
                            checked={!!checkedRows[`${section.id}-sub-${rIdx}`]}
                            onChange={() => setCheckedRows(prev => ({ ...prev, [`${section.id}-sub-${rIdx}`]: !prev[`${section.id}-sub-${rIdx}`] }))}
                          />

                          <DropdownCell value={row.logic} width={150} />
                          <DropdownCell value={row.feature} width={240} />
                          <DropdownCell value={row.op} width={160} />
                          <DropdownCell value={row.value} width={336} />

                          <div className="w-[16px] shrink-0" />
                        </div>
                      ))}
                    </div>
                  ))}
                </div>

                {/* 하단 필터 수식 영역 */}
                <div className="bg-white p-[16px] rounded-[12px] shrink-0">
                  <div className="font-medium text-[17px] leading-[1.1] text-[#929090] tracking-[-0.68px] flex flex-col gap-[4px] font-['Inter']">
                    <p className="m-0">Inclusion [ &nbsp;&#123;AGE &gt; 55&#125;&nbsp; &nbsp;</p>
                    <p className="m-0">
                      <span className="text-[#3a11d8] font-bold">And</span> &#123; WEIGHT &gt; 50 &#125; &nbsp;
                    </p>
                    <p className="m-0">
                      <span className="text-[#3a11d8] font-bold">And</span> &#123;MMTOTSCORE &gt;= 22 or CDRTOT = 0.5 or CDRTOT = 1&#125;
                    </p>
                    <p className="m-0">
                      <span className="text-[#3a11d8] font-bold">And</span> &#123;City = Seoul&#125; &nbsp;]&nbsp;
                    </p>
                  </div>
                </div>
              </div>
            </div>

            {/* 하단 버튼 */}
            <div className="shrink-0 flex justify-end gap-[12px] px-[12px] pb-[12px]">
              <button
                onClick={() => router.push("/drd/default-setting")}
                className="flex items-center justify-center h-[42px] px-[24px] py-[6px] rounded-[36px] bg-[#787776] border-none cursor-pointer"
              >
                <span className="font-['Inter'] font-semibold text-[17px] leading-[1.05] text-white tracking-[-0.51px]">Cancel</span>
              </button>
              <button
                onClick={() => { setCompleted("filter", true); router.push("/drd/default-setting"); }}
                className="flex items-center justify-center h-[42px] px-[24px] py-[6px] rounded-[36px] bg-[#f06600] border-none cursor-pointer"
              >
                <span className="font-['Inter'] font-semibold text-[17px] leading-[1.05] text-white text-center tracking-[-0.51px]">Confirm</span>
              </button>
            </div>
          </div>
</div>
        </div>
    </AppLayout>
  );
}